<template>
	<div id="app" class="text-center">
		<h1>在线翻译</h1>
		<h5 class="text-muted">简单 / 易用 / 便捷</h5>
		<translateForm v-on:formSubmit="translateText"></translateForm>
		<TranslateOutput v-bind:variable="translatedText"></TranslateOutput>
	</div>
</template>

<script>
import TranslateForm from './components/TranslateForm'
import TranslateOutput from './components/TranslateOutput'

export default {
	name: 'App',
	data:function(){
	return{
	translatedText:""
	}
	},
	components: {
		TranslateForm,
		TranslateOutput
		
	},
	methods:{
	translateText:function(text,language){
	this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?&key=trnsl.1.1.20180729T100232Z.335faeb01c658ae2.e5ee481126ffc59a9bd3e21e5ba8ba733e813e4e&lang='+language+'&text='+text).then((response)=>{
	//console.log(response.body.text[0]);
	this.translatedText=response.body.text[0];
	});
	}
	}
	
}
</script>

<style>
#app{
	margin:50px auto;
	/* border:solid 1px red; */

}
	h1{
		font-weight:700;
		font-size:45px;
		
	}
	h5{
		margin: 10px auto;
	}
</style>



