<template>
<div class="container">
    <div  class="row translateFom">
        <div class="col-md-6 col-md-offset-3" >
            <form id="transForm" class=" well form-inline">
                <!-- well 添加form表单的灰色样式 -->
                <input class="form-control" type="text" v-model="textToTranslate" placeholder="请输入翻译内容"/>
                <select class="form-control" v-model="language">
                    <option value="en">English</option>
                    <option value="ko">Korean</option>
                    <option value="ru">Russian</option>
                    <option value="ja">Janpenese</option>
                </select>
                <input class="btn btn-primary" v-on:click="formSubmit" type="submit" value="翻译"/>
            </form>
            </div>
        </div>
        </div>
</template>





<script>
    export default {
        name:'translateForm',
        data() {
            return {
                textToTranslate:'',
                language:''
                
            }
        },
        methods:{
            formSubmit:function (e) {
                e.preventDefault();
                this.$emit("formSubmit",this. textToTranslate,this.language);
            }
        },
        created:function(){//实例创建之后调用，最早拿到data数据，但拿不到el
            this.language='en';
        }
    }
</script>
<style>
#transForm{
    border-radius:10px;
    border:1px #ccc solid;
}
</style>



